import * as monaco from 'monaco-editor-core';

export type Nullable<T> = T | null | undefined;

export enum ResponseType {
    Server,
    Language,
    Format,
    Execute,
    Workspace
}

export enum TargetType {
    language,
    execute,
    executeInteract,
    format,
    workspace
}

export interface Response {
    id: string;
    type: ResponseType;
    results?: any;
    error?: string;
}

export interface InitializeWorkspaceCommand {
    sources?: { [path: string]: string };
    preview?: string;
}

export interface LanguageCommand {
    id?: string;
    lookup: string;
    column: number;
    line: number;
    path: string;
    config: any;
}

export interface ExecuteCommand {
    sourcePath: string;
}

export interface ExecuteInteractCommand {
    data: string;
}

export interface FormatCommand {
    sourcePath: string;
    range?: monaco.IRange;
}

export interface ClientUpdateSource {
    updatedSourceCode: string;
    updatedSourcePath: string;
}

export interface Request {
    id: string;
    cmd: InitializeWorkspaceCommand | LanguageCommand | ExecuteCommand | ExecuteInteractCommand | FormatCommand;
    type: TargetType;
    sourceUpdates?: ClientUpdateSource[];
    receivedTimeStamp?: number; // Filled by server
}
