import { RespondingWebSocket } from './responding-websocket';
import * as monaco from 'monaco-editor-core';
// tslint:disable-next-line: max-line-length
import { Response, TargetType, LanguageCommand, ExecuteCommand, ExecuteInteractCommand, FormatCommand, InitializeWorkspaceCommand, Request, ClientUpdateSource } from './types'
import * as uuid from 'uuid';

export enum LanguageCommandType {
  completion,
  definitions,
  tooltip,
  names,
  arguments,
  syntax
}

export class LanguageServerClient extends RespondingWebSocket<Response, 'id'> {
  public onWorkspaceInitializeHook: null | undefined | (() => { [path: string]: string });
  constructor(url: string, initializeWorkspace = true) {
    const socketOptions = {
      maxReconnectionDelay: 10000,
      minReconnectionDelay: 1000,
      reconnectionDelayGrowFactor: 1.3,
      connectionTimeout: 10000,
      maxRetries: Infinity,
      debug: false
    };
    super('id', url, [], socketOptions);
    if (initializeWorkspace) {
      this.onopen = (async (e: Event) => {
        let sources = null;
        if (this.onWorkspaceInitializeHook) {
          sources = this.onWorkspaceInitializeHook();
        }
        await this.sendRequest(TargetType.workspace, this.generateInitializeWorkspaceCommand(sources || {}));
      }) as any;
    }
  }

  public async sendRequest(
    type: TargetType,
    cmd: InitializeWorkspaceCommand | LanguageCommand | ExecuteCommand | ExecuteInteractCommand | FormatCommand,
    sourceUpdates?: ClientUpdateSource[]): Promise<Response> {
    const request = {
      id: uuid.v4(),
      cmd,
      type,
      sourceUpdates
    } as Request;

    this.send(JSON.stringify(request));
    const response = await this.listenForResponse(request.id);

    return response;
  }

  public generateLanguageCommand(type: LanguageCommandType, sourcePath: string, position?: monaco.Position): LanguageCommand {
    return {
      lookup: LanguageCommandType[type],
      column: (position?.column || 1) - 1,
      line: (position?.lineNumber || 1) - 1,
      path: sourcePath,
      config: {
        extraPaths: [],
        useSnippets: true,
        caseInsensitiveCompletion: true,
        showDescriptions: true,
        fuzzyMatcher: true
      }
    } as LanguageCommand;
  }

  public generateFormatCommand(sourcePath: string, range: monaco.Range) {
    return {
      sourcePath,
      range
    } as FormatCommand;
  }

  public generateInitializeWorkspaceCommand(sourceMap?: { [path: string]: string }) {
    return {
      sources: sourceMap || {}
    } as InitializeWorkspaceCommand;
  }

  public generateExecuteCommand(targetSource: string) {
    return {
      sourcePath: targetSource
    } as ExecuteCommand;
  }

  public generateExecuteInteractCommand(message: string) {
    return {
      data: message
    } as ExecuteInteractCommand;
  }
}
